import React, { Component } from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import PropTypes from 'prop-types'
export default class Tabs extends Component {
  constructor () {
    super()
    this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this)
    this.state = {
      curIndex:0,
      flagBarPos:0
    }
    this.handleTab = this.handleTab.bind(this)
  }
  static propTypes = {
    tabs:PropTypes.array,
    active:PropTypes.number,
    callback: PropTypes.func
  }
  setLinkBarPos = type => {
    let flagBarPos,tabArr = this.props.tabs;
    tabArr.length && tabArr.map((item,index)=>{
      if(type==index){
        flagBarPos = parseFloat( (100/tabArr.length) * index + (50/tabArr.length)) + '%'
      }
    })
    this.setState({flagBarPos})
  }
  static defaultProps = {
    tabs:['选项卡一','选项卡二'],
    active:0
  }
  componentWillMount(){
    this.setLinkBarPos(this.props.active);
  }
  handleTab(num){
    if(num!==this.state.curIndex){
      this.setState({
        curIndex:num
      })
      this.setLinkBarPos(num);
      this.props.callback && this.props.callback(num)
    }
  }
  render () {
    let { tabs } = this.props
    let { curIndex } = this.state
    return (
      <div className="tablink-box">
        <ul className="tab-list">
          {
            tabs.length && tabs.map((item, index) => {
              return (
                <li key={index} onClick={()=>this.handleTab(index)} className={curIndex==index?'tab-link active':'tab-link' }>{item}</li>
              )
            })
          }
        </ul>
        <i className="nav-linkbar" style={{left: this.state.flagBarPos}}></i>
      </div>
    )
  }
}
