import React, { Component } from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import PropTypes from 'prop-types';
import common from '../../utils/formattime'
export default class ActivityList extends Component {
  constructor (props) {
    super(props)
    this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
    this.state = {

    }
  }
  static propTypes = {
    gap:PropTypes.bool,
    list:PropTypes.array,
    tab:PropTypes.number
  }
  static defaultProps = {
    gap:true
  }
  render () {
    let { list,tab } = this.props;
    console.log(list)
    let activityList = list.length > 0 ? 
        list.map((item,index)=>{
        return <li key={index} className="activity-signlist">
          <img className="activity-image" src={item.actCoverImage} alt="" />
          <div className="activity-info">
            <h3 className="info-title">{item.actTitle}</h3>
            <div className="info-desc">{item.actDesc}</div>
              <p className="info-time">{common.formatDateTime(item.actStartTime,'yyyy-MM-dd hh:mm')}</p>
            <div className="info-location">{item.actAddress}</div>
            <p className="info-tags">{item.actLabel}</p>
          </div>
          <a href="javascript:;" className={item.actApplyStatus ? "info-btn info-end" : 'info-btn'}>{item.actApplyStatus?'报名结束':'立即报名'}</a>
        </li>
        })
      : []
          
    return (
      <div>
        {
          list.length>0?
           <ul className={ this.props.gap? 'activity-list':'activity-list activity-list2'}>
              {activityList}
            {/* <li>
              <img className="activity-image" src="http://10.10.82.235:9998/fortune/resource/img/paid_content_img1.png" alt="" />
              <div className="activity-info">
                <h3 className="info-title">大数据教你精准把握大势机会大数据教你精准把握大势机会大数据教你精准把握大势机会大数据教你精准把握大势机会</h3>
                <div className="info-desc">2018年最后一季度大盘是涨是跌？</div>
                <p className="info-time">2018-09-02 14:00-17:00</p>
                <div className="info-location">上海宛平南路营业部</div>
              </div>
            </li>
            <li>
              <img className="activity-image" src="http://10.10.82.235:9998/fortune/resource/img/paid_content_img1.png" alt="" />
              <div className="activity-info">
                <h3 className="info-title">大数据教你精准把握</h3>
                <div className="info-desc">2018年最后一季度大盘是涨是跌？</div>
                <p className="info-time">2018-09-02 14:00-17:00</p>
                <div className="info-location">上海宛平南路营业部</div>
              </div>
            </li> */}
           </ul>
          :
          <div className="empty-data">
            <i className="empty-activity"></i>
            <div className="empty-content"  dangerouslySetInnerHTML={
              {
                __html: tab == 0  ? "暂时无待参加活动 <br />快去发现更多活动吧 ！":
                      (tab == 1? `还没有关注任何内容<br />关注你感兴趣的社区，及时获取最新社区动态<br /><a href="#/myactivity" class="see-myactivity">赶紧去看看</a>`
                :"暂时还没有活动哟" )
              }
            }
            ></div>
          </div>
        }
      </div>
    )
  }
}
